@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
	margin: 0;
	padding: 0;
	color: #656C7A;
	font: 14px/18px "Poppins","Helvetica Neue",Verdana,Arial,sans-serif;
}

.app-name {
	font-size: 22px;
	text-transform: none;
	font-weight: bold;
	color: #656C7A;
}

.header {
	box-shadow: 0 0 2px rgba(0,0,0,.5);
    background-color: #FFFFFF;
}

.navbar {
	padding: 20px 30px;
}

.nav-item {
	display: block;
    padding: 0.5rem 1.5rem;
	cursor: pointer;
	font-size: 16px;
	font-weight: bold;
}

.nav-item:hover {
	color: #FF4641;
}

.nav-item.active {
	font-weight: bold;
	border-bottom: 2px solid #ffcd41;
	color: #A7ABB3;
}

.nav-link {
	padding: 0;
}
.menu-name {
	padding: 10px;
	font-size: 16px;
	font-weight: bold;
}

.menu-header {
	padding: 15px;
}
.tabulator-col-content {
	background-color: #F4F0ED;
}

.tabulator .tabulator-footer .tabulator-page.active {
	color: #d71e28;
	font-weight: bold;
}

.tabulator, .tabulator-row, .tabulator-cell, .tabulator-header,
.tabulator-col, .tabulator-sortable, .tabulator-col-title {
	font-size: 12px;
}

.data-count {
	font-size: 12px;
	font-weight: bold;
	margin: 20px 0 0 0;
}

.above-table-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: flex-start;
}

.download, .download:hover {
    display: block;
    width: 135px;
    margin: 10px 0;
    background: #5174b8;
    padding: 10px;
    text-align: center;
    color: white;
    font-weight: bold;
    text-decoration: none;
}

.download:hover {
	background: #2655af;
}
.chart-container {
	padding: 10px;
}

.content {
    padding: 15px 0;
}

.flex-container {
    display: flex;
    flex-flow: row wrap;
}

.flex-container div {
    margin: 5px 20px;
    min-width: 250px;
}

.label {
    margin: 0 10px;
}

.emphasis {
    font-weight: bold;
}

.section-header {
    font-size: 14px;
    font-weight: bold;
}

.overall-metric {
    color: #081788;
}

.stats-container {
    background-color: #f4f0ed;
    padding: 15px 20px;
}
